import { createRouter, createWebHistory } from '@ionic/vue-router';
import TabsPage from '../views/TabsPage.vue'
import tab1 from '../views/Tap1Page.vue'
import tab2 from '../views/Tab2Page.vue'
import tab3 from '../views/Tab3Page.vue'
import tab4 from '../views/Tab4Page.vue'

const routes = [
  {
    path: '/',
    redirect: '/tabs/tab1'
  },
  {
    path: '/tabs/',
    component: TabsPage,
    children: [
      {
        path: '',
        redirect: '/tabs/tab1'
      },
      {
        path: '/tabs/tab1',
        name: 'tab1',
        component: tab1
      },
      {
        path: '/tabs/tab2',
        name: 'tab2',
        component: tab2
      },
      {
        path: '/tabs/tab3',
        name: 'tab3',
        component: tab3
      }
      ,
      {
        path: '/tabs/tab4',
        name: 'tab4' ,
        component: tab4
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
